<template>
    <div class="other" style="background-color: #fff;">
        <van-nav-bar
                title="登录"
                fixed
                left-text="返回"
                left-arrow
                @click-left="$router.back()"
        />
        <div class="top">
            登录/注册刷题平台账号
        </div>
        <div class="login">
            <van-field v-model="phone" placeholder="请输入账号"  type="tel" label="账号" />
            <van-field v-model="password" placeholder="请输入账号密码"  type="password" label="密码" />
        </div>
        <div class="login1">
            <van-button style="width: 100%;" @click="userLogin" :disabled="!isLogin">登录/注册</van-button>
        </div>
    </div>
</template>

<script>
    import {check_phone} from "../../../common/js/myFun";
    import {userLogin} from "../../../api";
    import config from "../../../config";
    import {Toast} from "vant";

    export default {
        name: "Login",
        data(){
            return{
                phone: '',
                password: ''
            }
        },
        computed:{
            isLogin(){
                let {phone, password} = this;
                return check_phone(phone) && password.length !== 0
            },
        },
        methods:{
            async userLogin(){
                let {phone, password} = this;
                let data = await userLogin({
                    phone, password
                });
                if(data.code === 200){
                    config.setCache('userId', data.result.userInfo.id);
                    this.$store.dispatch('addUserInfo', {userInfo: data.result.userInfo});
                    this.$router.replace('/user/mine');
                    Toast(data.data);
                }else{
                    Toast(data.data);
                }
            }
        }
    }
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
    .other{
        >.top{
            width: 100%
            height: 6rem
            font-size: 2rem
            display flex
            align-items center
            justify-content center
            margin-top: 3rem
            margin-bottom: 2rem
        }
        >.login1{
            width: 100%
            margin-top: 2rem
            padding: 2rem
        }
    }
</style>
